<template>
  <div class="index-wrap">
    <div class="index-left">
      <div class="index-left-block">
        <h2>全部产品</h2>
        <template v-for='product in productList'>
            <h3>{{ product.title }}</h3>
            <ul >
              <li v-for='item in product.list'>
                <a :href="item.url">
                  {{ item.name}}
                  <span v-if='item.hot' class='hot-tag'>HOT</span>
                </a>
              </li>
            </ul>
            <div v-if='!product.last' class="hr"></div>
        </template>
      </div>
      <div class="index-left-block lastest-news">
        <h2>最新消息</h2>
        <ul>
          <li v-for="item in newsList">
            <a :href="item.url" class="new-item">{{ item.title }}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="index-right">
      <slider :sliderdata='sliderdata'></slider>
      <div class="index-board-list">
        <div class="index-board-item" 
             v-for='(item,index) in boardList' 
             :class="[{'line-last' : index % 2 !== 0}, 'index-board-' + item.id]">
          <div class="index-board-item-inner">
            <h2>{{item.title}}</h2>
            <p>{{item.description}}</p>
            <router-link class="button" :to="{path: 'detail/' + item.toKey}">立即购买</router-link>            
          </div>
        </div>
      </div>
    </div>
  	
  </div>
</template>

<script>
  import slider from '../components/slider'

  
	export default{
    components:{
      slider
    },
    // created:function  () {
    //   this.$http.get('api/getNewsList').then(
    //     function(res){
    //       this.newsList = res.body;
    //     },
    //     function(err){
    //       console.log(err);
    //     });
    // },
		data(){
			return{
        sliderdata: [
          {
            src: require('../assets/slideShow/pic1.jpg'),
            title: 'xxx1',
            href: 'detail/analysis'
          },
          {
            src: require('../assets/slideShow/pic2.jpg'),
            title: 'xxx2',
            href: 'detail/count'
          },
          {
            src: require('../assets/slideShow/pic3.jpg'),
            title: 'xxx3',
            href: 'http://xxx.xxx.com'
          },
          {
            src: require('../assets/slideShow/pic4.jpg'),
            title: 'xxx4',
            href: 'detail/forecast'
          }
        ],
				productList:{
          pc: {
            title: 'PC产品',
            list: [
              {
                name: '数据统计',
                url: 'http://starcraft.com'
              },
              {
                name: '数据预测',
                url: 'http://warcraft.com'
              },
              {
                name: '流量分析',
                url: 'http://overwatch.com',
                hot: true
              },
              {
                name: '广告发布',
                url: 'http://hearstone.com'
              }
            ]
          },
          app: {
            title: '手机应用类',
            last: true,
            list: [
              {
                name: '91助手',
                url: 'http://weixin.com'
              },
              {
                name: '产品助手',
                url: 'http://twitter.com',
                hot: true
              },
              {
                name: '智能地图',
                url: 'http://maps.com'
              },
              {
                name: '团队语音',
                url: 'http://phone.com'
              }
            ]
          }					
				},
        boardList: [
          {
            title: '开放产品',
            description: '开放产品是一款开放产品',
            id: 'car',
            toKey: 'analysis',
            saleout: false
          },
          {
            title: '品牌营销',
            description: '品牌营销帮助你的产品更好地找到定位',
            id: 'earth',
            toKey: 'count',
            saleout: false
          },
          {
            title: '使命必达',
            description: '使命必达快速迭代永远保持最前端的速度',
            id: 'loud',
            toKey: 'forecast',
            saleout: true
          },
          {
            title: '勇攀高峰',
            description: '帮你勇闯高峰，到达事业的顶峰',
            id: 'hill',
            toKey: 'publish',
            saleout: false
          }
        ],
        newsList:[
                  {
                    "id": 1,
                    "title": "习近平：为决胜全面小康社会实现中国梦而奋斗",
                    "url": "http://news.xinhuanet.com/politics/2017-07/27/c_1121391548.htm"
                  },
                  {
                    "id": 2,
                    "title": "三大运营商相继宣布：9月1日起取消长途漫游费",
                    "url": "http://www.cankaoxiaoxi.com/roll10/bd/20170727/2213614.shtml"
                  },
                  {
                    "id": 3,
                    "title": "苹果发布iTunes电脑版12.6.2更新，支持高DPI显示器",
                    "url": "http://www.sohu.com/a/159152727_114760"
                  },
                  {
                    "id": 4,
                    "title": "中国手机支付让现金过时:两大科技巨头'坐金矿上'",
                    "url": "http://finance.sina.com.cn/stock/usstock/c/2017-07-23/doc-ifyihrmf3197886.shtml"
                  }
                ],
			}
		}
	}
</script>

<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-left {
  float: left;
  width: 300px;
  text-align: left;
}
.index-right {
  float: left;
  width: 900px;
}
.index-left-block {
  margin: 15px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
}
.index-left-block .hr {
  margin-bottom: 20px;
}
.index-left-block h2 {
  background: #4fc08d;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: bold;
  color: #222;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.index-board-list {
  overflow: hidden;
}
.index-board-item {
  float: left;
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner{
  background: url(../static/img/1.png) no-repeat;
}
.index-board-loud .index-board-item-inner{
  background: url(../static/img/2.png) no-repeat;
}
.index-board-earth .index-board-item-inner{
  background: url(../static/img/3.png) no-repeat;
}
.index-board-hill .index-board-item-inner{
  background: url(../static/img/4.png) no-repeat;
}
.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}
.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 512px;
}
.hot-tag {
  background: red;
  color: #fff;
}
.new-item {
  display: inline-block;
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.button{
  margin-top: 18px;

}
</style>


