<template>
	<div class="loginForm">
		<div class="form-line">
			<span class="lab">用户名:</span>
			<input type='text' v-model="usrModal">
		</div>
		<div class="form-line">
			<span class="lab">密码:</span>
			<input type='text' v-model="passwordModal">
		</div>
		<div class="form-line">
			<span class="lab"></span>
			<a class="btnlogin" @click="login">登录</a>
			<span class="errtext">{{errText}}</span>
		</div>		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				usrModal:'',
				passwordModal:'',
				errText:'',
	
			}
		},
		methods:{
			login(){
				if((this.usrModal=='vue')&&(this.passwordModal=='vue')){
					this.errText='';
					this.$emit('login',this.usrModal);
				}else{
					this.errText='密码或用户名错误！'			
				}
			}
		}
	}
</script>

<style scoped>
	.form-line{
        padding:10px 0;

	}
	.form-line input{
        border:1px solid #ccc;
        height:30px;
        line-height:30px;
        padding:0px 10px;
	}
	.lab{
		font-size:16px;
		display:inline-block;
		width:100px;
	}
	.btnlogin{
		
		color:white;
		padding:10px 20px;
		display:inline-block;
		background-color:#4fc08d;
	}
</style>


